*, ::after, ::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
  font-family: "Roboto", sans-serif;
  transition: all 0.3s cubic-bezier(0.39, 0.575, 0.565, 1);
}

.contenedor {
  width: 90%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: 100%;
}

i {
  width: 2rem;
  margin-right: 2rem;
}

.contenido {
  position: absolute;
  width: calc(100% - 8rem);
  min-height: calc(100vh - 8rem);
  top: 8rem;
  left: 8rem;
  transition: all 0.3s ease-in-out;
  padding-top: 5rem;
}
.contenido.redimensionar {
  left: 25rem;
  width: calc(100% - 25rem);
}
@media (max-width: 768px) {
  .contenido {
    left: 0;
    width: 100%;
  }
}
@media (max-width: 480px) {
  .contenido.redimensionar {
    left: 0;
    width: 100%;
  }
}
.contenido #cont-paginas {
  height: 100%;
  margin-bottom: 10rem;
}
.contenido #cont-paginas .dinamico {
  width: 100%;
  height: 100%;
  border: none;
}

.introducciones {
  width: 100%;
  height: 60rem;
  overflow: hidden;
  position: relative;
}
@media (max-width: 480px) {
  .introducciones {
    height: 80vh;
  }
}

.header {
  width: 100%;
  height: 8rem;
  background: #f5f5f5;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  z-index: 20;
  transition: all 0.3s cubic-bezier(0.39, 0.575, 0.565, 1);
}
.header .icon__menu {
  width: 8rem;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 1rem;
}
.header .icon__menu i {
  font-size: 2rem;
  cursor: pointer;
  position: absolute;
  color: #3b5895;
}
.header h1 {
  color: #3b5895;
}
@media (max-width: 480px) {
  .header h1 {
    font-size: 2.4rem;
  }
}
.header .logo {
  width: 15rem;
  height: 6rem;
  margin-right: 3rem;
}
@media (max-width: 768px) {
  .header .logo {
    width: 10rem;
  }
}
@media (max-width: 480px) {
  .header .logo {
    width: 8rem;
    height: 4.5rem;
  }
}

.menu__side {
  width: 8rem;
  height: 100%;
  background: #3b5895;
  position: fixed;
  top: 8rem;
  left: 0;
  font-size: 1.8rem;
  z-index: 21;
  overflow: hidden;
  overflow-y: scroll;
  border-right: 2rem solid #3b5895;
  transition: all 0.3s cubic-bezier(0.39, 0.575, 0.565, 1);
  box-shadow: 14px -1px 4px -11px rgba(0, 0, 0, 0.59);
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.menu__side .name__page {
  color: #ffffff;
  padding: 2rem 3rem;
  display: flex;
  align-items: center;
}
.menu__side::-webkit-scrollbar {
  display: none;
}
.menu__side.side__move {
  width: 25rem;
}
@media (max-width: 768px) {
  .menu__side {
    left: -25rem;
    width: 25rem;
  }
  .menu__side.side__move {
    left: 0rem;
  }
}

.barra__progreso {
  padding: 2rem 3rem;
}
.barra__progreso .barra {
  width: 100%;
  height: 0.3rem;
  background: #ffffffb2;
  margin-bottom: 1rem;
  position: relative;
  display: none;
}
.barra__progreso .barra.barra_ver {
  display: block;
}
.barra__progreso .barra .porcentaje {
  position: absolute;
  background: #ffffff;
  height: 100%;
  top: 0;
  left: 0;
  transition: width 0.3s ease-in;
}
.barra__progreso #progreso__mensaje {
  font-size: 2rem;
  color: #ffffff;
}

.options__menu {
  padding: 2rem 3rem;
  position: absolute;
  top: 18rem;
}
.options__menu a {
  color: #ffffffb2;
  cursor: default;
  display: block;
  position: relative;
  transition: all 0.3s ease;
}
.options__menu a:hover {
  color: #ffffff;
}
.options__menu a.select {
  color: #ffffff;
  transition: all 0.3s ease;
}
.options__menu a.select::before {
  content: "";
  width: 0.3rem;
  height: 80%;
  position: absolute;
  top: 10%;
  left: -3rem;
  background: #ffffff;
}
.options__menu a .option {
  padding: 2rem 0;
  display: flex;
  align-items: center;
  position: relative;
}
.options__menu a .option i {
  cursor: pointer;
}
.options__menu a .option h4 {
  font-weight: 300;
  cursor: pointer;
}

.btn_nav {
  position: fixed;
  width: 30rem;
  height: 6rem;
  right: 10rem;
  bottom: 0;
  left: 40%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  background: rgba(61, 137, 199, 0.699);
  border-top-left-radius: 2rem;
  border-top-right-radius: 2rem;
  z-index: 100;
}
@media (max-width: 768px) {
  .btn_nav {
    left: 30%;
  }
}
@media (max-width: 480px) {
  .btn_nav {
    left: 10%;
  }
}
.btn_nav i {
  width: auto;
  margin: 0;
  font-size: 4.2rem;
  color: #ffffff;
  cursor: pointer;
  transition: all 0.2s ease-in;
}
.btn_nav i:hover {
  color: #3b5895;
}

#seccion1 {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  position: relative;
}
@media (max-width: 768px) {
  #seccion1 {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 1rem;
  }
}
#seccion1::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: #4d92aa;
  z-index: -1;
}
#seccion1 .cont-vineta {
  display: inline-block;
  justify-content: center;
  align-items: flex-start;
}
#seccion1 .cont-vineta #vineta01 {
  left: 2rem;
  margin-top: 6rem;
  border: 5px solid #3b5895;
  background: #ffffff;
  width: 90%;
  padding: 2rem;
  border-radius: 2rem;
  line-height: 1.6;
  position: relative;
}
@media (max-width: 768px) {
  #seccion1 .cont-vineta #vineta01 {
    margin-top: 1rem;
  }
}
#seccion1 .cont-vineta #vineta01::before {
  content: "";
  position: absolute;
  top: 20%;
  left: calc(100% + 0.4rem);
  width: 3rem;
  height: 4rem;
  background: #3b5895;
  clip-path: polygon(0 0, 0% 100%, 100% 50%);
}
@media (max-width: 768px) {
  #seccion1 .cont-vineta #vineta01::before {
    top: calc(100% + 0.4rem);
    left: 40%;
    width: 4rem;
    height: 3rem;
    clip-path: polygon(0 0, 100% 0%, 50% 100%);
  }
}
#seccion1 .cont-vineta #vineta01 #frase_01 {
  color: #3b5895;
  font-weight: 500;
}
#seccion1 .cont-vineta #vineta01 #frase_02 {
  color: #129a63;
  font-weight: 700;
}
#seccion1 .cont-vineta #vineta02 {
  left: 2rem;
  margin-top: 2rem;
  border: 5px solid #3b5895;
  background: #ffffff;
  width: 90%;
  padding: 2rem;
  border-radius: 2rem;
  line-height: 1.6;
  position: relative;
}
@media (max-width: 768px) {
  #seccion1 .cont-vineta #vineta02 {
    margin-top: 1rem;
  }
}
#seccion1 .cont-vineta #vineta02::before {
  content: "";
  position: absolute;
  top: 20%;
  left: calc(100% + 0.4rem);
  width: 3rem;
  height: 4rem;
  background: #3b5895;
  clip-path: polygon(0 0, 0% 100%, 100% 50%);
}
@media (max-width: 768px) {
  #seccion1 .cont-vineta #vineta02::before {
    top: calc(100% + 0.4rem);
    left: 40%;
    width: 4rem;
    height: 3rem;
    clip-path: polygon(0 0, 100% 0%, 50% 100%);
  }
}
#seccion1 .cont-vineta #vineta02 #frase_01 {
  color: #3b5895;
  font-weight: 500;
}
#seccion1 .cont-imagen {
  max-height: 100%;
}

